html,
body {
  background-color: #282c34;
  margin: 0;
  padding: 0 10px 0 10px;
  color: #dcdfe4;
}

h1 {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 30px
}

section {
  border-radius: 50px;
}

.monaco-wrap {
  padding: 5px;
  background-color: #383a42;
}

#input-wrap>span {
  font-weight: bold;
  vertical-align: middle;
  color: #e5c07b
}

#input-wrap {
  text-align: center;
  margin: 0 auto;
  white-space: nowrap;
}

#id-input {
  height: 30px;
  width: 180px;
  background-color: #21252b;
  border: none;
  font-weight: bold;
  outline: none;
  color: #dcdfe4;
}

.buttons {
  text-align: center;
}

button {
  margin: 20px;
  height: 40px;
  width: 120px;
  outline: none;
  border: 4px solid #56b6c2;
  background-color: #21252b;
  font-size: 15px;
  font-weight: bold;
  color: #dcdfe4;
}